.friend__list {
  margin: 0 auto;
  margin-bottom: 50px;
  padding: 0;
  width: 420px;
  list-style: none;
}

.item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 20px;
  border: 1px solid #626262;
  margin-bottom: 10px;
}

.status {
  margin-right: 20px;
  height: 20px;
  width: 20px;
  border-radius: 50%;
}

.avatar {
  margin-right: 20px;
}

.statusActive {
  background-color: green;
}

.statusInactive {
  background-color: red;
}
