.profile {
  margin: 0 auto;
  margin-bottom: 50px;
  padding: 60px;
  width: 300px;
  background-color: #ede9e9;
}

.description {
  margin: 0 auto;
  padding-top: 40px;
  text-align: center;
  background-color: #ffffff;
  border-radius: 5px;
}

.avatar {
  border-radius: 50%;
}

.stats {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
  text-align: center;
}

.stats__item {
  display: flex;
  flex-direction: column;
}
